// components/ChatBox.js
import { useEffect, useState, useRef } from "react";
import { io } from "socket.io-client";
import api from "../utils/api";

export default function ChatBox({ post, user, isAdmin }) {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");
  const messagesEndRef = useRef(null);
  const socketRef = useRef(null);

  useEffect(() => {
    // Initialize socket
    socketRef.current = io("http://localhost:5000");

    // Join the post-specific room
    socketRef.current.emit("joinRoom", post?._id);

    // Listen for new messages
    socketRef.current.on("newMessage", (msg) => {
      setMessages((prev) => [...prev, msg]);
      scrollToBottom();
    });

    return () => {
      socketRef.current.disconnect();
    };
  }, [post?._id]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  const sendMessage = async () => {
    if (!input.trim()) return;

    const msgData = {
      postId: post?._id,
      postTitle: post?.title,
      postPrice: post?.price,
      postImage: post?.images?.[0] || "",
      sender: user?.name,
      senderId: user?.id,
      message: input,
      isAdmin: isAdmin || false,
      createdAt: new Date(),
    };

    try {
      // 1ï¸âƒ£ Send to backend database
      await api.post("/api/messages/send", msgData);

      // 2ï¸âƒ£ Send through socket for real-time updates
      socketRef.current.emit("sendMessage", msgData);

      setMessages((prev) => [...prev, msgData]);
      setInput("");
      scrollToBottom();
    } catch (err) {
      console.error("Message send error:", err);
    }
  };

  return (
    <div className="fixed bottom-4 right-4 w-80 max-h-96 bg-white border rounded-xl shadow-lg flex flex-col">
      <div className="bg-blue-600 text-white p-2 rounded-t-xl font-bold">
        Live Chat (Trust Market)
      </div>

      <div className="flex-1 overflow-y-auto p-2 space-y-2">
        {messages.map((m, i) => (
          <div
            key={i}
            className={`p-2 rounded-lg max-w-[75%] break-words ${
              m.senderId === user?.id
                ? "bg-blue-500 text-white ml-auto"
                : "bg-gray-300 text-gray-800"
            }`}
          >
            {m.message}
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>

      <div className="flex p-2 border-t">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          className="flex-1 border rounded-lg px-2 py-1 focus:outline-none focus:ring-1 focus:ring-blue-500"
          placeholder="Type a message..."
          onKeyDown={(e) => e.key === "Enter" && sendMessage()}
        />
        <button
          onClick={sendMessage}
          className="ml-2 bg-blue-600 text-white px-3 py-1 rounded-lg hover:bg-blue-700"
        >
          Send
        </button>
      </div>
    </div>
  );
}
